@import "assets/styles/settings/_settings";

.alert {
  align-items: flex-start;
  padding: map-get($s-sizes, md);
  margin: map-get($s-sizes, md) 0;
  background-color: $s-color-gray-lighter;
  border-left: 2px solid;
  animation-duration: 0.3s;
}

.grid {
  display: flex;
  margin-right: map-get($s-sizes, md) * -1;
  margin-left: map-get($s-sizes, md) * -1;
}

.cell {
  flex: 0 1 auto;
  padding-right: map-get($s-sizes, md);
  padding-left: map-get($s-sizes, md);

  &-grow {
    flex-basis: auto;
    flex-grow: 1;
  }
}

/* Icon */
.icon {
  flex-shrink: 0;

  svg {
    font-size: map-get($s-sizes, lg);
    vertical-align: middle;
  }
}

/* Content */
.content {
  padding-left: 0;

  *:first-child {
    margin-top: 0;
  }

  *:last-child {
    margin-bottom: 0;
  }
}

/* Close */
.close {
  flex-shrink: 0;

  &-button {
    all: unset;
    padding: 0;
    line-height: 1;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    outline: 0;

    svg {
      font-size: 1rem;
      vertical-align: middle;
    }
  }
}

/* Variants */
.success {
  border-color: $s-color-success;
  .icon {
    color: $s-color-success;
  }
}

.warning {
  border-color: $s-color-warning;
  .icon {
    color: $s-color-warning;
  }
}

.danger {
  border-color: $s-color-danger;
  .icon {
    color: $s-color-danger;
  }
}

.closable {
  .content {
    padding-right: 0;
  }
}

.fixed-bottom,
.fixed-bottom-right {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: map-get($s-layers, "alert");
  margin-bottom: 0;
  box-shadow: map-get($s-shadows, md);
}

.fixed-bottom-right {
  @media (min-width: map-get($s-breakpoints, sm)) {
    max-width: map-get($s-breakpoints, sm);
    margin-right: map-get($s-sizes, sm);
  }
}
